'use client'

import { useMemo } from 'react'
import { useSearchParams } from 'next/navigation'
import { Loader2 } from 'lucide-react'

/**
 * YouTube URLÏóêÏÑú video IDÎ•º Ï∂îÏ∂úÌïòÎäî Ìï®Ïàò
 * ÏßÄÏõê ÌòïÏãù:
 * - https://www.youtube.com/watch?v=nKpZFe-fx2Q
 * - https://youtu.be/nKpZFe-fx2Q
 * - https://www.youtube.com/embed/nKpZFe-fx2Q
 */
function getYouTubeVideoId(url: string): string | null {
  if (!url) return null

  try {
    const urlObj = new URL(url)
    const hostname = urlObj.hostname.toLowerCase()

    // YouTube ÎèÑÎ©îÏù∏ Ï≤¥ÌÅ¨
    if (!hostname.includes('youtube.com') && !hostname.includes('youtu.be')) {
      return null
    }

    // 1. watch?v= ÌòïÏãù: https://www.youtube.com/watch?v=nKpZFe-fx2Q
    if (urlObj.searchParams.has('v')) {
      return urlObj.searchParams.get('v')
    }

    // 2. youtu.be ÌòïÏãù: https://youtu.be/nKpZFe-fx2Q
    if (hostname.includes('youtu.be')) {
      const pathname = urlObj.pathname.replace(/^\//, '')
      return pathname || null
    }

    // 3. embed ÌòïÏãù: https://www.youtube.com/embed/nKpZFe-fx2Q
    const embedMatch = urlObj.pathname.match(/\/embed\/([^/?]+)/)
    if (embedMatch) {
      return embedMatch[1]
    }

    return null
  } catch {
    // URL ÌååÏã± Ïã§Ìå® Ïãú Ï†ïÍ∑úÏãùÏúºÎ°ú ÏßÅÏ†ë Ï∂îÏ∂ú
    // watch?v= ÌòïÏãù
    const watchMatch = url.match(/[?&]v=([^&]+)/)
    if (watchMatch) return watchMatch[1]

    // youtu.be ÌòïÏãù
    const shortMatch = url.match(/youtu\.be\/([^/?]+)/)
    if (shortMatch) return shortMatch[1]

    // embed ÌòïÏãù
    const embedMatch = url.match(/\/embed\/([^/?]+)/)
    if (embedMatch) return embedMatch[1]

    return null
  }
}

/**
 * URLÏóêÏÑú videoIDÎ•º Ï∂îÏ∂úÌïòÎäî Ìï®Ïàò
 * Ïòà: ?videoID=123 ÎòêÎäî ?videoId=123
 * ÎòêÎäî YouTube URLÏóêÏÑú v ÌååÎùºÎØ∏ÌÑ∞ Ï∂îÏ∂ú
 */
function getVideoIdFromUrl(searchParams: URLSearchParams, currentUrl?: string): string | null {
  // 1. ÏßÅÏ†ë videoID ÌååÎùºÎØ∏ÌÑ∞ ÌôïÏù∏
  const directVideoId = searchParams.get('videoID') || searchParams.get('videoId') || null
  if (directVideoId) return directVideoId

  // 2. ÌòÑÏû¨ URLÏù¥ YouTube URLÏù∏ Í≤ΩÏö∞
  if (currentUrl) {
    const youtubeId = getYouTubeVideoId(currentUrl)
    if (youtubeId) return youtubeId
  }

  return null
}

/**
 * Referer URLÏóêÏÑú videoIDÎ•º Ï∂îÏ∂úÌïòÎäî Ìï®Ïàò
 * YouTube URLÎèÑ ÏßÄÏõê
 */
function getVideoIdFromReferer(referer: string | null): string | null {
  if (!referer) return null

  // 1. YouTube URLÏóêÏÑú Ï∂îÏ∂ú ÏãúÎèÑ
  const youtubeId = getYouTubeVideoId(referer)
  if (youtubeId) return youtubeId

  // 2. ÏùºÎ∞ò videoID ÌååÎùºÎØ∏ÌÑ∞ ÌôïÏù∏
  try {
    const url = new URL(referer)
    return url.searchParams.get('videoID') || url.searchParams.get('videoId') || null
  } catch {
    // URL ÌååÏã± Ïã§Ìå® Ïãú referer Î¨∏ÏûêÏó¥ÏóêÏÑú ÏßÅÏ†ë Ï∂îÏ∂ú ÏãúÎèÑ
    const match = referer.match(/[?&]videoID=([^&]+)|[?&]videoId=([^&]+)/i)
    return match ? (match[1] || match[2]) : null
  }
}

export default function ViewerHomePage() {
  const searchParams = useSearchParams()
  
  // videoID Ï∂îÏ∂ú (useMemoÎ°ú ÏµúÏ†ÅÌôî)
  const { videoId, referer } = useMemo(() => {
    const currentUrl = typeof window !== 'undefined' ? window.location.href : null
    const currentReferer = typeof window !== 'undefined' ? document.referrer : null

    // 1. ÌòÑÏû¨ URLÏóêÏÑú videoID Ï∂îÏ∂ú (YouTube URL Ìè¨Ìï®)
    const urlVideoId = getVideoIdFromUrl(searchParams, currentUrl || undefined)
    
    // 2. RefererÏóêÏÑú videoID Ï∂îÏ∂ú (YouTube URL Ìè¨Ìï®)
    const refererVideoId = getVideoIdFromReferer(currentReferer)

    // 3. Ïö∞ÏÑ†ÏàúÏúÑ: ÌòÑÏû¨ URL > Referer
    const finalVideoId = urlVideoId || refererVideoId

    // ÎîîÎ≤ÑÍπÖÏö© (Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑúÎßå)
    if (process.env.NODE_ENV === 'development') {
      console.log('Current URL:', currentUrl)
      console.log('Current URL videoID:', urlVideoId)
      console.log('Referer:', currentReferer)
      console.log('Referer videoID:', refererVideoId)
      console.log('Final videoID:', finalVideoId)
    }

    return {
      videoId: finalVideoId,
      referer: currentReferer,
    }
  }, [searchParams])

  // TODO: videoIDÎ•º ÏÇ¨Ïö©ÌïòÏó¨ ÏòÅÏÉÅ Îç∞Ïù¥ÌÑ∞ Î°úÎìú
  // useEffect(() => {
  //   if (videoId) {
  //     fetchVideoData(videoId)
  //   }
  // }, [videoId])

  return (
    <div className="min-h-screen flex flex-col items-center justify-center bg-gray-50">
      {/* Î°úÍ≥† */}
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900">üì¶ Bookae</h1>
      </div>

      {/* Î°úÎî© Ïä§ÌîºÎÑà */}
      <div className="flex flex-col items-center gap-4">
        <Loader2 className="w-12 h-12 text-blue-600 animate-spin" />
        <p className="text-gray-600 text-lg">Î°úÎî© Ï§ë...</p>
      </div>

      {/* ÎîîÎ≤ÑÍπÖ Ï†ïÎ≥¥ (Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑúÎßå) */}
      {process.env.NODE_ENV === 'development' && (
        <div className="mt-12 p-4 bg-white rounded-lg shadow-sm border border-gray-200 text-sm text-gray-600 max-w-md">
          <div className="space-y-2">
            <div>
              <strong>VideoID:</strong> {videoId || 'ÏóÜÏùå'}
            </div>
            <div>
              <strong>Referer:</strong> {referer || 'ÏóÜÏùå'}
            </div>
          </div>
        </div>
      )}
    </div>
  )
}

